import React, {useState, useEffect} from "react";

import socket from '../../config/socket';
import api from '../../config/Api';


// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Table from "components/Table/Table.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";

import "antd/dist/antd.css";
import 'react-toastify/dist/ReactToastify.css';
import { Menu, Dropdown, Divider, DatePicker } from 'antd';
import { CalendarOutlined, DownOutlined} from '@ant-design/icons';
import { flexbox } from "@material-ui/system";

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0"
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF"
    }
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1"
    }
  }
};
const dateFormatList = ['DD/MM/YYYY', 'DD/MM/YY'];



const useStyles = makeStyles(styles);


export default function TableList() {
  const [tipo, setTipo] = useState('tipo');
  const [horario, setHorario] = useState('horario');
  const [data, setData] = useState( new Date().toLocaleDateString());
  const [agendamentos,setAgendamentos] = useState([]);
  const menuHorario = (
    <Menu  onClick={ async (e) => { await setHorario(e.key);  }} key='horario'>
      <Menu.ItemGroup>
        <Menu.Item key='manha'>Manha</Menu.Item>
        <Divider></Divider>
        <Menu.Item key='tarde'>Tarde </Menu.Item>
        <Divider></Divider>
        <Menu.Item key='horario'>horario</Menu.Item>
      </Menu.ItemGroup>
      
    </Menu>
  );

  const menuTipo = (
    <Menu onClick={  e =>setTipo(e.key) } key='tipo'>
      <Menu.ItemGroup>
        <Menu.Item key='natural'>Natural</Menu.Item>
        <Divider></Divider>
        <Menu.Item key='artificial'>Artificial </Menu.Item>
        <Divider></Divider>
        <Menu.Item key='tipo'>tipo</Menu.Item>
      </Menu.ItemGroup>
    </Menu>
  );
  

  useEffect(filtro,[tipo,horario,data]);
    
  useEffect(()=>{
    
    socket.delete(data =>{
      console.log('teste table');
        filtro();
      });

      socket.insert(data =>{
        console.log('teste table');
          filtro();
        });
  },[])
 
    function  filtro(){ 
    let filtro =
    {
      tipo,
      horario,
      data
    }
    tipo === 'tipo' && delete filtro['tipo'];
    horario === 'horario' && delete filtro['horario'];
    data === 'data' && delete filtro['data'];

     const userToken = localStorage.getItem('token');
     const config = {
            headers: { Authorization: `Bearer ${userToken}` }
        };
        async function loadAgendamentos(){
           let agenda = [];
          await api.post( 
                    'agendamentos/',
                  filtro,
                  config
                ).then(
                      (response)=>{

                       response.data.agendamentos.map((agendamento)=>{
                            let arr = [agendamento.nome,agendamento.tipo, agendamento.horario, agendamento.data];

                            agenda.push(arr)
                       });
                  }
                  ).catch(
                   
                    console.log
                    
                    );

             setAgendamentos(agenda);     
    }
     loadAgendamentos();
 }

  const classes = useStyles();
  return (
    <GridContainer>
      
      <GridItem xs={12} sm={12} md={12}>
        <Card>
          <CardHeader color="primary">
            <h4 className={classes.cardTitleWhite}>Agendamentos</h4>
            <p className={classes.cardCategoryWhite}>
              Bronzeamento
            </p>
            <div style={{display:'flex', flexDirection:'row' ,alignItems:'center', justifyContent:'center'}  }>
            <div style={{marginRight:'10px'}}>
            <Dropdown overlay={menuHorario}>
                  <a className="ant-dropdown-link" id='horario' onClick={e => e.preventDefault()} href='http://191.252.223.69/'>
                  <h6 className={classes.cardTitleWhite}> {horario} <DownOutlined color='#fff' /></h6>
                  </a>
                   </Dropdown>
            
            </div> 

            <div style={{marginRight:'10px'}}>
            <Dropdown overlay={menuTipo} >
                  <a className="ant-dropdown-link" id='tipo' onClick={e => e.preventDefault()} href='#' >
                  <h6 className={classes.cardTitleWhite}> {tipo} <DownOutlined color='#fff' /></h6>
                  </a>
                   </Dropdown>
            
            </div>
              <DatePicker    format={dateFormatList}  className='data-picker' onChange={(date,dateString)=>{

                    dateString === '' ? setData(new Date().toLocaleDateString()) :  setData(dateString);

               }} />
            </div>                   
          </CardHeader>
          <CardBody>
            <Table
              tableHeaderColor="primary"
              tableHead={["Nome", "Tipo", "Horario", "Data"]}
              tableData={agendamentos}
            />
          </CardBody>
        </Card>
      </GridItem>
      
    </GridContainer>
  );
}
