import React from "react";
import "antd/dist/antd.css";
import { List, Avatar,Button,message,Popover} from 'antd';
import { CheckOutlined,DeleteOutlined } from '@ant-design/icons';
import Tooltip from "@material-ui/core/Tooltip";
import { makeStyles } from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";

import Edit from "@material-ui/icons/Edit";
import Close from "@material-ui/icons/Close";
import Check from "@material-ui/icons/Check";

const useStyles = makeStyles(styles);
export default function CustomListAvatar(props){
const classes = useStyles();
    
return (

    <List style={{minHeight:'300px'}}
    pagination={{ pageSize: props.pagination }} 
    scroll={{ y: 240 }}
    itemLayout="horizontal"
    dataSource={props.dataSource}
    renderItem={item => (
      <List.Item key ={item._id}>
        <List.Item.Meta key={item._id}
          avatar={<Avatar src={item.thumbnail_url} />}
          title={<a href="">{item.nome}</a>}
          description={<p> Tipo: {item.tipo} Horário: {item.horario} {item.data} </p>}
        />
        <Tooltip
                id="tooltip-top"
                title="Finalizar"
                placement="top"
                classes={{ tooltip: classes.tooltip }}
              >
               <Button type="primary" icon={<CheckOutlined />} size='small'  onClick={() => { props.finalizar(item._id)}}/>
              </Tooltip>
              
              <Tooltip
                id="tooltip-top"
                title="Cancelar"
                placement="top"
                classes={{ tooltip: classes.tooltip }}>
                  <Button type="primary" danger icon={<DeleteOutlined />} size='small' style={{marginLeft:'10px'}}  onClick={() => {message.info(item._id);}}/>
              </Tooltip>
        
        
      </List.Item>
    )}
  />);

}