import socketClient from 'socket.io-client';

class socket{
     
   
    
    constructor(props){
     const io = null;   
     this.io =  socketClient('http://191.252.223.69:3333',{
    autoConnect: false,
  });
    }
 


 socketConnect(){
      this.io.connect();
}

  socketDisconnect() {
    if(this.io.connected){
      this.io.disconnect()
    }
  }


  notification(getNotification){
    this.io.on('notificacao', getNotification);
  }

  insert(getInsert){
    this.io.on('insert',getInsert);
  }
  delete(getDelete){
    this.io.on('delete',getDelete);
  }

}
  export default new socket();

 
 
