import React, { useEffect,useState } from "react";
import classNames from "classnames";
import PropTypes from "prop-types";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Hidden from "@material-ui/core/Hidden";
// @material-ui/icons
import Menu from "@material-ui/icons/Menu";
// core components
import AdminNavbarLinks from "./AdminNavbarLinks.js";
import RTLNavbarLinks from "./RTLNavbarLinks.js";
import Button from "components/CustomButtons/Button.js";

import 'react-toastify/dist/ReactToastify.css';

import { toast, ToastContainer } from 'react-toastify';
import socket from '../../config/socket';
import api from '../../config/Api';


import styles from "assets/jss/material-dashboard-react/components/headerStyle.js";

const useStyles = makeStyles(styles);

export default function Header(props) {

const [notification,setNotification] = useState([]);
const [badge,setBadge] = useState(0);
const [websocket,setWebsocket] = useState('');
 
  useEffect(()=>{
    console.log('debug socket')
    socket.notification(data =>{
    toast.success(data);
    loadNotification();
    });
  },[websocket]);

  useEffect(()=>{
    
    loadNotification();
    
      
    },[]);
    
  


  function loadNotification(){
   
    const userToken = localStorage.getItem('token');
    const config = {
     headers: { Authorization: `Bearer ${userToken}` }
            };
          api.get( 
                   'notificacao/',
                      config
                    ).then(
                         
                       (response)=>{
                            setNotification(response.data.notificacoes); 
                            setBadge(response.data.notificacoes.length); 

                           }
                      
                      ).catch(
                       
                        console.log
                        
                        )
                }

                function deleteAllNotification(){
   
                  const userToken = localStorage.getItem('token');
                  const config = {
                   headers: { Authorization: `Bearer ${userToken}` }
                          };
                        api.delete( 
                                 'notificacao/',
                                    config
                                  ).then(
                                        
                                         loadNotification()
                                    
                                    ).catch(
                                     
                                      console.log
                                      
                                      )
                              }              


  const classes = useStyles();
  function makeBrand() {
    var name;
    props.routes.map(prop => {
      if (window.location.href.indexOf(prop.layout + prop.path) !== -1) {
        name = props.rtlActive ? prop.rtlName : prop.name;
      }
      return null;
    });
    return name;
  }
  const { color } = props;
  const appBarClasses = classNames({
    [" " + classes[color]]: color
  });
  return (
    
    <AppBar className={classes.appBar + appBarClasses}>
      <ToastContainer />
      <Toolbar className={classes.container}>
        <div className={classes.flex}>
          {/* Here we create navbar brand, based on route name */}
          <Button color="transparent" href="#" className={classes.title}>
            {makeBrand()}
          </Button>
        </div>
        <Hidden smDown implementation="css">
          {props.rtlActive ? <RTLNavbarLinks /> : <AdminNavbarLinks notification = {notification} badge={badge} deleteNotification={deleteAllNotification} />}
        </Hidden>
        <Hidden mdUp implementation="css">
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={props.handleDrawerToggle}
          >
            <Menu />
          </IconButton>
        </Hidden>
      </Toolbar>
    </AppBar>
  );
}

Header.propTypes = {
  color: PropTypes.oneOf(["primary", "info", "success", "warning", "danger"]),
  rtlActive: PropTypes.bool,
  handleDrawerToggle: PropTypes.func,
  routes: PropTypes.arrayOf(PropTypes.object)
};
