import React from "react";
import "antd/dist/antd.css";
import { List, Avatar,Button,message} from 'antd';
import { MessageOutlined } from '@ant-design/icons';

export default function CustomList(props){
    
    
return (

    <List 
    style={{minHeight:'300px'}}
    pagination={{ pageSize: props.pagination }} 
    scroll={{ y: 240 }}
    itemLayout="horizontal"
    dataSource={props.dataSource}
    renderItem={item => (
      <List.Item key ={item._id}>
        <List.Item.Meta key={item._id}
          avatar={<Avatar src={item.thumbnail_url} />}
          title={<a href="">{item.nome}</a>}
          description={<p>Ultimo Agendamento: {item.ultimo_agendamento} </p>}
        />
        <Button type="Success" shape="round" icon={<MessageOutlined />} size="large"  onClick={() => {
        message.info(item._id);
      }}/>
      </List.Item>
    )}
  />);

}