import React,{useState,useEffect} from "react";


// react plugin for creating charts
import ChartistGraph from "react-chartist";
// @material-ui/core
import { makeStyles } from "@material-ui/core/styles";
import Icon from "@material-ui/core/Icon";
// @material-ui/icons
import Store from "@material-ui/icons/Store";
import Warning from "@material-ui/icons/Warning";
import DateRange from "@material-ui/icons/DateRange";
import LocalOffer from "@material-ui/icons/LocalOffer";
import Update from "@material-ui/icons/Update";
import ArrowUpward from "@material-ui/icons/ArrowUpward";
import AccessTime from "@material-ui/icons/AccessTime";
import Accessibility from "@material-ui/icons/Accessibility";
import BugReport from "@material-ui/icons/BugReport";
import Code from "@material-ui/icons/Code";
import Cloud from "@material-ui/icons/Cloud";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Table from "components/Table/Table.js";
import Tasks from "components/Tasks/Tasks.js";
import CustomTabs from "components/CustomTabs/CustomTabs.js";
import Danger from "components/Typography/Danger.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardIcon from "components/Card/CardIcon.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import List from "components/List/List.js";
import ListAvatar from "components/ListAvatar/ListAvatar.js";
//import ant
import "antd/dist/antd.css";


import api from '../../config/Api';
import socket from '../../config/socket';
import Auth from '../../config/Auth';



import {
  dailySalesChart,
  emailsSubscriptionChart,
  completedTasksChart
} from "variables/charts.js";

import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";

const useStyles = makeStyles(styles);

export default function Dashboard() {
  const classes = useStyles();
  const [agendamentos,setAgendamentos] = useState([]);
  const[usuario,setUsuario] = useState([]);
  const[receita,setReceita] = useState([]);
  const[data,setData] = useState(new Date().toLocaleDateString());
  

  useEffect(()=>{
    loadUsuarios();
    },[]);
  useEffect(filtro,[]);  
  useEffect(()=>{
    socket.delete(data =>{
        filtro();
      });

      socket.insert(data =>{
          filtro();
        });
  },[]);

  useEffect(()=>{
    loadReceita();
  },[]);

  function loadUsuarios(){
     
    async function load(){
      const userToken = localStorage.getItem('token');
      const config = {
            headers: { Authorization: `Bearer ${userToken}` }
      };
     await api.get( 
               'usuario/',
             config      
           ).then(
                 (response)=>{
                    setUsuario(response.data);          
                  }
             ).catch(
               console.log 
               );      
    }
        load();     
  }
 
    function  filtro(){
    let filtro =
    {
     data,
     aberto:true
    }
     const userToken = localStorage.getItem('token');
     const config = {
            headers: { Authorization: `Bearer ${userToken}` }
        };
        async function loadAgendamentos(){
           
          await api.post( 
                    'agendamentos/',
                  filtro,
                  config
                ).then(
                      (response)=>{
                      setAgendamentos(response.data.agendamentos);
                  }
                  ).catch(
                    console.log
                    );         
    }
     loadAgendamentos();
 }

 function  finalizar(id){

   const userToken = localStorage.getItem('token');
   const config = {
          headers: { Authorization: `Bearer ${userToken}`,
                     agendamento_id: id }
      };
      async function load(){
        const body = {}
        await api.put( 
                  '/admin/finalizar',
                body,
                config
              ).then(
                    (response)=>{
                      console.log(response);
                      filtro();
                      loadReceita();
                }
                ).catch(
                  console.log
                  );         
  }
   load();
}

 function  loadReceita(){
   const userToken = localStorage.getItem('token');
   const config = {
          headers: { Authorization: `Bearer ${userToken}` }
      };
      async function load(){
         
        await api.get( 
                  '/admin/receita/',
                config
              ).then(
                    (response)=>{
                    
                    response.data.receita.map((valor)=>{
                      setReceita(valor.receita);
                    });
                }
                ).catch(
                  console.log
                  );         
  }
   load();
}

  return (
    <div>
      <GridContainer>
        <GridItem xs={12} sm={6} md={3}>
          <Card>
            <CardHeader color="warning" stats icon>
              <CardIcon color="warning">
                <Icon>content_copy</Icon>
              </CardIcon>
              <p className={classes.cardCategory}>Agendamentos</p>
              <h3 className={classes.cardTitle}>
              {agendamentos.length} 
              </h3>
            </CardHeader>
            <CardFooter stats>
              <div className={classes.stats}>
                <Danger>
                  <Warning />
                </Danger>
                <a href="#pablo" onClick={e => e.preventDefault()}>
                 Dia: {data}
                </a>
              </div>
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={6} md={3}>
          <Card>
            <CardHeader color="success" stats icon>
              <CardIcon color="success">
                <Store />
              </CardIcon>
              <p className={classes.cardCategory}>Receita do Dia</p>
              <h3 className={classes.cardTitle}>${receita},00</h3>
            </CardHeader>
            <CardFooter stats>
              <div className={classes.stats}>
                <DateRange />
                Last 24 Hours
              </div>
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={6} md={3}>
          <Card>
            <CardHeader color="danger" stats icon>
              <CardIcon color="danger">
                <Icon>info_outline</Icon>
              </CardIcon>
              <p className={classes.cardCategory}>Cancelamentos</p>
              <h3 className={classes.cardTitle}>15</h3>
            </CardHeader>
            <CardFooter stats>
              <div className={classes.stats}>
                <LocalOffer />
                Tracked from Github
              </div>
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={6} md={3}>
          <Card>
            <CardHeader color="info" stats icon>
              <CardIcon color="info">
                <Accessibility />
              </CardIcon>
              <p className={classes.cardCategory}>Novos Clientes</p>
              <h3 className={classes.cardTitle}>45</h3>
            </CardHeader>
            <CardFooter stats>
              <div className={classes.stats}>
                <Update />
                Just Updated
              </div>
            </CardFooter>
          </Card>
        </GridItem>
      </GridContainer>
      <GridContainer>
        <GridItem xs={12} sm={12} md={4}>
          <Card chart>
            <CardHeader color="success">
              <ChartistGraph
                className="ct-chart"
                data={dailySalesChart.data}
                type="Line"
                options={dailySalesChart.options}
                listener={dailySalesChart.animation}
              />
            </CardHeader>
            <CardBody>
              <h4 className={classes.cardTitle}>Receita Mensal</h4>
              <p className={classes.cardCategory}>
                <span className={classes.successText}>
                  <ArrowUpward className={classes.upArrowCardCategory} /> 55%
                </span>{" "}
                 Rendimento em relação ao mês Passado.
              </p>
            </CardBody>
            <CardFooter chart>
              <div className={classes.stats}>
                <AccessTime /> updated 4 minutes ago
              </div>
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={12} md={4}>
          <Card chart>
            <CardHeader color="warning">
              <ChartistGraph
                className="ct-chart"
                data={emailsSubscriptionChart.data}
                type="Bar"
                options={emailsSubscriptionChart.options}
                responsiveOptions={emailsSubscriptionChart.responsiveOptions}
                listener={emailsSubscriptionChart.animation}
              />
            </CardHeader>
            <CardBody>
              <h4 className={classes.cardTitle}>Novos Clientes</h4>
              <p className={classes.cardCategory}>Media de novos Clintes do mês</p>
            </CardBody>
            <CardFooter chart>
              <div className={classes.stats}>
                <AccessTime /> campaign sent 2 days ago
              </div>
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={12} md={4}>
          <Card chart>
            <CardHeader color="danger">
              <ChartistGraph
                className="ct-chart"
                data={completedTasksChart.data}
                type="Line"
                options={completedTasksChart.options}
                listener={completedTasksChart.animation}
              />
            </CardHeader>
            <CardBody>
              <h4 className={classes.cardTitle}>Cancelamentos</h4>
              <p className={classes.cardCategory}>Media de Cancelamentos pela plataforma</p>
            </CardBody>
            <CardFooter chart>
              <div className={classes.stats}>
                <AccessTime /> campaign sent 2 days ago
              </div>
            </CardFooter>
          </Card>
        </GridItem>
      </GridContainer>
      <GridContainer>
        <GridItem xs={12} sm={12} md={6}>
          <CustomTabs
            title="Tasks:"
            headerColor="primary"
            tabs={[
              {
                tabName: "Usuarios",
                tabIcon: BugReport,
                tabContent: (
                  <List
                  dataSource={usuario}
                  pagination="3"
                  ></List>
                )
              },
              {
                tabName: "Usuarios Ativos",
                tabIcon: Code,
                tabContent: (
                  <Tasks
                    checkedIndexes={[0]}
                    tasksIndexes={[0, 1]}
                    tasks={usuario}
                  />
                )
              },
              {
                tabName: "Usuarios Com fidelidade",
                tabIcon: Cloud,
                tabContent: (
                  <Tasks
                    checkedIndexes={[1]}
                    tasksIndexes={[0, 1, 2]}
                    tasks={usuario}
                  />
                )
              }
            ]}
          />
        </GridItem>
        <GridItem xs={12} sm={12} md={6}>
          <Card>
            <CardHeader color="warning">
              <h4 className={classes.cardTitleWhite}>Agendamentos</h4>
              <p className={classes.cardCategoryWhite}>
                Pagos adiantado
              </p>
            </CardHeader>
            <CardBody>
            <ListAvatar
                  dataSource={agendamentos}
                  pagination="3"
                  finalizar = {finalizar}
                  ></ListAvatar>
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    </div>
  );
}
